<template>
<div>
<!--面包屑导航-->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>商品管理</el-breadcrumb-item>
    <el-breadcrumb-item :to="{ path: '/goods'}">商品列表</el-breadcrumb-item>
    <el-breadcrumb-item>添加商品</el-breadcrumb-item>

  </el-breadcrumb>
<!--卡片视图-->
  <el-card>
<!--提示警告-->
    <el-alert
      title="添加商品"
      type="info"
      show-icon
      center
    :closable="false">
    </el-alert>
<!--步骤条-->
<!--active 用于激活选项-->
<!--将长得像数字的字符转化为数字，在字符串后边减0即可-->
    <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
      <el-step title="基本信息"></el-step>
      <el-step title="商品参数"></el-step>
      <el-step title="商品属性"></el-step>
      <el-step title="商品图片"></el-step>
      <el-step title="商品内容"></el-step>
      <el-step title="完成"></el-step>
    </el-steps>
    <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" label-position="top">
<!--标签页-->
<!--before-leave 根据judge函数返回的是true还是false 选择阻止标签页或者放行-->
<!--@tab-clic当切换标签页时,触发tabClicked,在tabClicked 函数中发送请求,获得数据    -->
    <el-tabs :tab-position="tabPosition" style="height: 100%;" v-model="activeIndex" :before-leave="judge" @tab-click="tabClicked">
      <el-tab-pane label="基本信息" name="0">
        <el-form-item label="商品名称" prop="goods_name">
          <el-input v-model="addForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="商品价格" prop="goods_price">
<!--type number 限制只能输入数字-->
          <el-input v-model="addForm.goods_price" type="number"></el-input>
        </el-form-item>
        <el-form-item label="商品重量" prop="goods_weight">
          <el-input v-model="addForm.goods_weight" type="number"></el-input>
        </el-form-item>
        <el-form-item label="商品数量" prop="goods_number">
          <el-input v-model="addForm.goods_number" type="number"></el-input>
        </el-form-item>
<!--级联选择器-->
        <el-form-item label="商品分类" prop="goods_cat">
<!--v-model 获取选择的数据id-->
<!--options数据来源-->
<!--props 级联选择器配置对象-->
        <el-cascader
          size="mini"
          v-model="addForm.goods_cat"
          :options="cateList"
          :props="cateProps"
          @change="parentCateChanged"
        ></el-cascader>
        </el-form-item>
      </el-tab-pane>
<!--多选框-->
      <el-tab-pane label="商品参数" name="1" >
        <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
          <el-checkbox-group v-model="item.attr_vals">
<!--每循环一次,拿到一个cb, CB就是名字-->
            <el-checkbox :label="cb" v-for="(cb,i) in item.attr_vals" :key="i" border></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-tab-pane>
<!--商品属性-->
      <el-tab-pane label="商品属性" name="2">
        <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
          <el-input v-model="item.attr_vals"></el-input>
        </el-form-item>
      </el-tab-pane>
<!--图片上传-->
      <el-tab-pane label="商品图片" name="3">
<!--uoload上传组件-->
<!--action 上传图片请求的api接口-->
<!--list-type 预览图片的呈现方式-->
        <el-upload
          :action="uploadURL"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          list-type="picture"
        :headers="headerObj"
        :on-success="handleSuccess">
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-tab-pane>
      <el-tab-pane label="商品内容" name="4">
<!--富文本编辑器组件-->
        <quill-editor v-model="addForm.goods_introduce"></quill-editor>
<!--添加商品按钮-->
        <el-button type="primary" @click="add">添加商品</el-button>
      </el-tab-pane>
    </el-tabs>
    </el-form>
  </el-card>
<!--图片预览-->
  <el-dialog
    title="图片预览"
    :visible.sync="previewVisible"
    width="50%">
    <img :src="priviewPath" class="previewImg">
  </el-dialog>
</div>
</template>

<script>
import _ from 'lodash'
export default {
  name: 'Add',
  data () {
    return {
      // 用于控制步骤
      activeIndex: '0',
      // 用于控制标签页的方向
      tabPosition: 'left',
      // 添加商品的表单数据对象
      addForm: {
        goods_price: 0,
        goods_name: '',
        goods_weight: 0,
        goods_number: 0,
        // 商品所属的分类数组
        goods_cat: [],
        // 图片数组
        pics: [],
        // 商品详情描述
        goods_introduce: '',
        // 用于存储attr_id和attr_value
        attrs: []

      },
      // 表单验证规则
      addFormRules: {
        goods_name: [
          { required: true, trigger: 'blur', message: '请输入商品名称' }
        ],
        goods_price: [
          { require: true, trigger: 'blur', message: '请输入商品价格' }
        ],
        goods_weight: [
          { required: true, trigger: 'blur', message: '请输入商品重量' }
        ],
        goods_number: [
          { required: true, trigger: 'blur', message: '请输入商品数量' }
        ],
        // 商品所属的分类数组
        goods_cat: [
          { required: true, trigger: 'blur', message: '请选择商品分类' }
        ]
      },
      // 选中父级分类的ID数组
      selectKeys: [],
      // 分类参数数据
      cateList: [],
      // 级联选择器配置对象
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 返回动态参数的列表数据
      sel: 'many',
      // 动态参数列表数据
      manyTableData: [],
      // 静态参数列表数据
      onlyTableData: [],
      // 上传图片URL地址
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 图片上传组件的请求头对象
      headerObj: {
        // 获取访问的token值
        Authorization: window.sessionStorage.getItem('token')
      },
      priviewPath: '',
      previewVisible: false

    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    async getCateList () {
      // 发起获取参数分类的请求
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取分类参数失败')
      }
      // 获取成功 把分类参数赋值给cateList
      this.cateList = res.data
    },
    // 分类数据处理
    parentCateChanged () {
      console.log(this.addForm.goods_cat)
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
        return this.$message.error('请选择三级分类')
      }
    },
    // 控制标签页的切换
    judge (activeName, oldActiveName) {
      // 即将离开的名字是否为0,并且判断级联选择器是否选中了三级分类
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error(' 请先选择商品分类')
        return false
      }
      return true
    },
    async tabClicked () {
      // 证明访问的是动态参数面板
      if (this.activeIndex === '1') {
        console.log('动态参数面板')
        const { data: res } = await this.$http.get(`categories/${this.cateID}/attributes`, { params: { sel: 'many' } })
        if (res.meta.status !== 200) {
          return this.$message.error('获取动态参数面板失败')
        }
        // 动态参数数据处理
        res.data.forEach(item => {
        // 每拿到一个item对象,周split做分割
        // ? 问好指的是,是否为空,否则就返回一个空白数组
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        })
        // 获取成功将数值赋值给return私有变量中的manyTableData
        this.manyTableData = res.data
        console.log(res.data)
      } else if (this.activeIndex === '2') {
        const { data: result } = await this.$http.get(`categories/${this.cateID}/attributes`, { params: { sel: 'only' } })
        if (result.meta.status !== 200) {
          this.$message.error('获取静态参数面板失败')
        }
        this.onlyTableData = result.data
        console.log(this.onlyTableData)
      }
    },
    // 处理图片预览效果
    handlePreview (file) {
      console.log(file)
      // 在return中创建此字符串,并将路径赋值给它
      this.priviewPath = file.response.data.url
      // 用dialog组件,把路径用<img src 格式渲染
      this.previewVisible = true
    },
    // 处理移除图片的操作
    handleRemove (file) {
      console.log(file)
      // 1.获取将要删除的图片的临时路径
      const filePath = file.response.data.tmp_path
      // 2.从pisc数组中,找到这个图片对应的索引值
      const i = this.addForm.pics.findIndex(x => x.pic === filePath)
      // 3.调用数组的splice方法,把图片信息,从pisc数组中删除
      this.addForm.pics.splice(i, 1)
      console.log(this.addForm)
    },
    // 获取上传图片的临时路径
    handleSuccess (response) {
      console.log(response.data.tmp_path)
      // 1. 拼接得到一个图片信息对象
      const picInfo = { pic: response.data.tmp_path }
      // 2.将图片信息对象push到pisc数组中
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    // 添加商品
    async add () {
      // 表单与验证
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('请填写必要的表单项')
        }
        // 指定添加的业务逻辑
        // 从数组变成字符串
        // 深拷贝 lodash cloneDeep(拷贝的内容),此数据与级联选择器的goods_cat 冲突,所以要深拷贝一份进行处理,在进行赋值
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        // 处理动态参数manyTableData
        // 循环的拿出每个item项
        this.manyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals.join(' ') }
          this.addForm.attrs.push(newInfo)
        })
        // 处理静态属性onlyTableData
        this.onlyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        console.log(form)
        // 发起请求添加商品, eg 商品的名称必须是唯一的
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status !== 201) {
          return this.$message.error('添加商品失败')
        }
        this.$message.success('添加商品成功')
        this.$router.push('/goods')
      })
    }

  },
  // 计算器
  computed: {
    // 三级分类ID
    cateID () {
      // 判断级联选择器选中的是否为三级分类,如果为三级分类,则return 返回addForm.goods_cat 索引为3的ID
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }

}
</script>

<style lang="less" scoped>
.el-steps{
  margin: 15px 15px 0px 0px ;
}
.el-tabs{
  margin: 15px 15px 0px 0px;
}
.el-checkbox{
  margin: 0 10px 0 0 !important;
}
.previewImg{
  width: 50%;
  height: 50%;
}
.el-button{
  margin: 15px 15px 0 0;
}
</style>
