import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../components/login'
import Home from '../components/Home.vue'
import welcome from '../components/Welcome.vue'
import users from '../components/user/users.vue'
import roles from '@/components/power/roles'
import rights from '../components/power/rights.vue'
import goods from '../components/goods/goods.vue'
import params from '../components/goods/params.vue'
import categories from '../components/goods/categories.vue'
import orders from '../components/order/orders'
import Add from '../components/goods/Add.vue'
import report from '../components/report/report.vue'
Vue.use(VueRouter)

const routes = [
  { path: '/login', component: login },
  { path: '/', redirect: '/login' },
  {
    path: '/home',
    component: Home,
    // redirect 重定向到/welcome
    redirect: '/welcome',
    // children子路由
    children: [
      // path 转跳路径
      // component 导入的名字
      // 在转跳路径为path中 ，展示component
      { path: '/welcome', component: welcome },
      { path: '/users', component: users },
      { path: '/roles', component: roles },
      { path: '/rights', component: rights },
      { path: '/goods', component: goods },
      { path: '/params', component: params },
      { path: '/categories', component: categories },
      { path: '/orders', component: orders },
      // 在home页面展示goods页面，如果写在外边，则转跳到别的页面进行展示
      { path: '/goods/Add', component: Add },
      // 数据报表路由转跳
      { path: '/reports', component: report }
    ]
  }
]

const router = new VueRouter({
  routes
})

// 挂载路由导航守卫（访问权限需求）
router.beforeEach((to, from, next) => {
  // to将要访问的路径
  // from 从哪个路径跳转而来
  // next 放行函数 表示放行
  // 放行的两种方法 1.直接放行 调用next() 2.next('/home') 强制跳转
  // 判断用户是否跳转到登陆页面，如果访问的是登陆页面(login)，则放行，如果不是，则判断此访问用户是否带有token，
  if (to.path === '/login') return next()
  // 如果访问的不是登录页面 则获取token
  // 获取session
  const tokenstr = window.sessionStorage.getItem('token')
  // 判断tokenstr是否为空值，如果为空，则调用next()函数强制跳转到登录页面
  if (!tokenstr) return next('/login')
  // 如果不为空，则放行到访问的页面
  next()
})
export default router
