import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './plugins/element.js'
import './assets/css/global.css'
import TreeTable from 'vue-table-with-tree-grid'
import axios from 'axios'
// 导入富文本编辑器
import VueQuillEditor from 'vue-quill-editor'
// 导入富文本编辑器的样式
import 'quill/dist/quill.core.css' // import styles
import 'quill/dist/quill.snow.css' // for snow theme
import 'quill/dist/quill.bubble.css' // for bubble theme
// 配置请求的根路径
axios.defaults.baseURL = 'http://127.0.0.1:8888/api/private/v1/'
axios.interceptors.request.use(config => {
  console.log(config)
  config.headers.Authorization = window.sessionStorage.getItem('token')
  return config
})
// 将富文本编辑器注册为全局可用的组件
Vue.use(VueQuillEditor)
Vue.prototype.$http = axios
Vue.config.productionTip = false
Vue.component('tree-table', TreeTable)
// 时间过滤器
Vue.filter('dataFormat', function (originVal) {
  const dt = new Date(originVal)
  const y = dt.getFullYear()
  // padStart 第一个参数是总长度多少位，第二个参数是如果不足多少位的话用哪个字符串来填充
  // 年月日时分秒
  // getMonth 是一个时间数值， + ''把他变成字符串
  const m = (dt.getMonth() + 1 + '').padStart(2, '0')
  // getDate 是一个时间数值， + ''把他变成字符串
  const d = (dt.getDate() + '').padStart(2, '0')

  // getHours 是一个时间数值 加上 '' 把他变成字符串
  const hh = (dt.getHours() + '').padStart(2, '0')
  // getMonth 是一个时间数值，加上'' 把他变成字符串
  const mm = (dt.getMonth() + '').padStart(2, '0')
  // getSeconds 是一个时间数值，加上'' 把它变成字符串
  const ss = (dt.getSeconds() + '').padStart(2, '0')
  return `${y}-${m}-${d} ${hh}:${mm}:${ss}`
})
// 注册
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
