<template slot-scope="scope">
<div>
<!--面包屑导航-->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/Home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>权限管理</el-breadcrumb-item>
    <el-breadcrumb-item>角色列表</el-breadcrumb-item>
  </el-breadcrumb>
<!--添加按钮-->
  <el-row :gutter="20">
    <el-col span="4">
      <el-button type="primary" @click="addroleDialogVisible = true">添加角色</el-button>
    </el-col>
  </el-row>
<!--卡片视图-->
  <el-card>
    <el-table :data="roleList" stripe border>
<!--prop既是数据来源,也是验证规则-->
<!--展开列-->
      <el-table-column type="expand">
        <template slot-scope="scope">
          <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
<!--渲染一级权限-->
            <el-col :span="5" >
              <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
              <!--小箭头-->
              <i class="el-icon-caret-right"></i>
            </el-col>
<!--渲染二级，三级权限-->
            <el-col :span="19">
<!--根据for循环渲染所有的二级权限 -->
              <el-row :class="[i2 === 0 ? '' : 'dbtop' ]" v-for="(item2,i2) in item1.children" :key="item2.id">
                <el-col :span="6">
                  <el-tag type="success" closable @close="removeRightById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                  <!--小箭头-->
                  <i class="el-icon-caret-right" ></i>
                </el-col>
<!--根据for循环渲染所有的三级权限-->
                <el-col :span="18">
                  <el-tag type="warning" v-for="(item3) in item2.children" :key="item3.id" closable @close="removeRightById(scope.row,item3.id)">{{item3.authName}}</el-tag>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
<!--          <pre>{{scope.row}}</pre>-->
        </template>
      </el-table-column>
      <!--type="index"索引列-->
<!--data 总数居来源-->
<!--label  标签名字-->
<!--prop 不仅仅是数据来源，也是校验规则-->
      <el-table-column type="index"></el-table-column>
      <el-table-column label="角色名称" prop="roleName"></el-table-column>
      <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
      <el-table-column label="操作" prop="level" width="300px">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)">编辑</el-button>
          <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteEditDialog(scope.row.id)">删除</el-button>
          <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)">分配权限</el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
<!--添加角色对话框-->
<!--在el-dialog添加关闭事件，当关闭对话框时触发函数 @close-->
<!--model 数据来源-->
<!--prop数据来源以及校验规则-->
<!--ref引用对象-->
<!--v-model 监听用户输入到input中的数据-->
  <el-dialog
    title="添加角色"
    :visible.sync="addroleDialogVisible"
    width="50%" @close="addDialogClose" rules="addFormRules">
    <el-form :model="addForm" ref="addFormRole" :rules="addFormRules" label-width="70px">
      <el-form-item label="角色名" prop="roleName">
        <el-input v-model="addForm.roleName"></el-input>
      </el-form-item>
      <el-form-item label="描述" prop="roleDesc">
        <el-input v-model="addForm.roleDesc"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="addroleDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addRole">确 定</el-button>
  </span>
  </el-dialog>
<!--修改角色信息-->
<!--v-model 监听用户输入到input中的数据-->
<!--ref引用对象-->
<!--prop数据来源以及校验规则-->
  <el-dialog
    title="提示"
    :visible.sync="editDialogVisible"
    width="50%"
    @close="editDialogClose">
    <el-form :model="editForm" ref="editFormRef" :rules="addFormRules" label-width="70px">
      <el-form-item label="角色名" prop="roleName">
        <el-input v-model="editForm.roleName" disabled></el-input>
      </el-form-item>
      <el-form-item label="描述" prop="roleDesc">
        <el-input v-model="editForm.roleDesc"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editRoleInfo">确 定</el-button>
  </span>
  </el-dialog>
<!--分配权限-->
  <el-dialog
    title="分配权限"
    :visible.sync="showSetRightdialogVisible"
    width="50%"
  @close="SetRightDialogClosed">
<!--树形控件-->
<!--props 树形数据的分配-->
<!--:data 数据来源-->
<!--show-checkbox node-key="id" 显示属性前边的小框-->
<!--default-expand-all="true" 点开分配权限弹出对话框直接显示一二三级的所有权限-->
<!--default-expand-all="true" 给角色的权限打勾-->
<!--defKeys 该角色的所有权限-->
  <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all="true" :default-checked-keys="defKeys" ref="treeRef"></el-tree>
    <span slot="footer" class="dialog-footer">
    <el-button @click="showSetRightdialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="allotRights">确 定</el-button>
  </span>
  </el-dialog>
</div>
</template>
<script>
export default {
  name: 'roles.vue',
  data () {
    return {
      // 返回所有角色列表
      roleList: [],
      // 控制添加角色的显示和隐藏
      addroleDialogVisible: false,
      // 获取输入的数据
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      // 校验规则
      addFormRules: {
        roleName: [
          { required: true, message: '请输入正确的角色名称', trigger: 'blur' },
          { min: 2, max: 5, message: '请输入2到5个字符之间', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '请输入正确的角色描述', trigger: 'blur' },
          { min: 2, max: 5, message: '请输入2到5个字符之间', trigger: 'blur' }
        ]
      },
      // 控制修改角色信息对话框的展示和隐藏
      editDialogVisible: false,
      // 存放查询角色信息的数组
      editForm: {},
      // 控制分配权限对话框的展开和隐藏
      showSetRightdialogVisible: false,
      // 所有权限的数据
      rightsList: [],
      // 树形控件的属性绑定对象
      treeProps: {
        // 父节点的名称
        label: 'authName',
        // 父子节点的嵌套
        children: 'children'
      },
      // 默认选中的节点的ID数组
      defKeys: [],
      // 当前即将分配权限的角色的Id
      roleId: ''
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    // 获取角色列表
    async getRolesList () {
    // 发起获取角色列表的请求
      const { data: res } = await this.$http.get('roles')
      // 请求数据之后，判断返回的验证码是否为200
      // 如果不是200 则return 返回 抛出错误提示 获取角色列表失败
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      // 如果为200 则把数据赋值给在私有数据变量中定义的roleList中
      this.roleList = res.data
      console.log(this.roleList)
    },
    // 监听对话框关闭事件，当表单填写数据之后，未提交，关闭后再次打开，数据应该重置，而不是保留
    addDialogClose () {
      this.$refs.addFormRole.resetFields()
    },
    // 添加角色列表
    async addRole () {
      // 发起添加角色的请求
      const { data: res } = await this.$http.post('roles', this.addForm)
      console.log(res.meta.status)
      // 对返回的数据状态码进行判断
      if (res.meta.status !== 201) {
        return this.$message.error('添加失败')
      }
      this.$message.success('添加成功')
      this.addroleDialogVisible = false
      // 隐藏对话框
      this.addDialogClose()
      this.getRolesList()
      this.roleList = res.data
    },
    // 查询角色ID
    async selectRoleId (id) {
      const { data: res } = await this.$http.get('roles/')
      console.log(res)
    },
    //  重置对话框
    editDialogClose () {
      this.$refs.editFormRef.resetFields()
    },
    // 点击展示修改对话框 并把旧数据布置在input中
    async showEditDialog (id) {
      // 点击展示修改对话框
      this.editDialogVisible = true
      // 发起修改请求
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      // 把数据赋值给存放角色数据的数组
      this.editForm = res.data
    },
    // 提交修改的用户信息
    async editRoleInfo () {
      // 发起修改角色的请求
      const { data: res } = await this.$http.put('roles/' + this.editForm.roleId, {
        roleName: this.editForm.roleName,
        roleDesc: this.editForm.roleDesc
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('角色信息修改失败')
      }
      this.$message.success('角色信息修改成功')
      // 并且关闭修改角色信息的对话框
      this.editDialogVisible = false
      // 刷新角色列表
      this.getRolesList()
    },
    // 根据ID删除角色信息
    async deleteEditDialog (id) {
      // 弹框提示是否删除
      const res = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
        // 如果不用catch捕获错误消息,则会报错
      }).catch(err => err)
      // 如果用户确认删除则返回字符串 confirm
      // 如果用户取消删除则返回字符串 cancel
      console.log(res)
      if (res !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      // 发送删除请求
      const { data: result } = await this.$http.delete('roles/' + id)
      if (result.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
      // 刷新角色列表
      this.getRolesList()
    },
    // 根据ID删除对应的权限
    // role.id ,角色id rightId 权限ID
    async removeRightById (role, rightId) {
    // 弹窗提示用户是否要删除或取消删除
      const confirmResult = await this.$confirm('此操作将永久删除该权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      // 判断点击后返回的字符串, 是选择确定还是取消
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      // 发送删除请求，请求中带了两个参数 分别是 角色ID 以及角色的权限ID
      const { data: res } = await this.$http.delete(`roles/${role.id}}/rights/${rightId}}`)
      // 发送请求后判断请求返回的状态码(status) 如果为200 则删除成功 ，如果不为200 则弹窗提示 删除失败
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      // 刷新角色列表 (每次增删改查成功后都要进行列表刷新)
      // this.getRolesList()
      // 删除后不让窗口自动闭合
      // 把删除后最新的列表赋值给当前的列表
      role.children = res.data
      this.defKeys = rightId
      console.log(rightId)
    },
    // 展示分配权限的对话框
    async showSetRightDialog (role) {
      this.roleId = role.id
      // 递归获取三级节点的ID
      this.getLeafKeys(role, this.defKeys)
      // 展开权限分配的对话框
      this.showSetRightdialogVisible = true
      // 获取所有权限的数据
      const { data: res } = await this.$http.get('rights/tree')
      // 判断返回状态码是否为200
      if (res.meta.status !== 200) {
        this.$message.error('获取失败')
      }
      // console.log(res)
      // 如果请求返回的状态码为200 则在私有变量区域 定义一个存放所有权限分配数据的一个数组(rightsList)用于存放数据
      this.rightsList = res.data
    },
    // 通过递归的形式，获取角色下的第三级权限
    getLeafKeys (node, arr) {
      // 如果当前节点不包含 children属性，则为三级节点
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    // 监听分配权限对话框的关闭事件
    SetRightDialogClosed () {
      // 权限重置
      this.defKeys = []
    },
    // 点击为角色分配权限
    async allotRights () {
      // 拿到半选以及全选的id
      const keys = [
        // 获取用户点击的权限的ID
        ...this.$refs.treeRef.getCheckedKeys(),
        // 获取用户点击的权限的ID
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      // 用逗号连接起来
      const idStr = keys.join(',')
      console.log(idStr)
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) {
        return this.$message.error('分配权限失败')
      }
      this.$message.success('分配权限成功')
      // 刷新列表
      this.getRolesList()
      // 隐藏对话框
      this.showSetRightdialogVisible = false
    }

  }
}
</script>
<style lang="less" scoped>
.el-tag{
  margin:7px;
}
.bdtop{
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
</style>
