<template>
    <el-container class="home-container">
<!--面包屑区域-->
    <el-header>
      <div>
        <img src="" alt="">
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
<!--页面主体区域-->
    <el-container>
<!--侧边栏-->
<!--"?"true ":"false-->
      <el-aside :width="isCollapse ? '64px' : '200px'">
          <div class="toggle-button" @click="toggleCollapse">|||</div>
<!--侧边栏菜单区-->
<!--添加: 属性绑定-->
<!--router="true激活导航" 开启路由模式-->
        <el-menu
          background-color="#333744"
          text-color="#fff" :unique-opened="true" :collapse="isCollapse"
          :collapse-transition="false"
          router
          :default-active="activePath">
          <!--一级菜单-->
          <!--绑定唯一的index-->
          <!--双重for循环 取数数据-->
          <!--只接受字符串不接受数值，解决方法，在后面拼接一段空字符串-->
          <el-submenu :index="item.id + ''" v-for="item in menulist" :key="item.id">
            <!--一级模板区域-->
            <template slot="title">
              <!--图标-->
              <i :class="iconList[item.id]"></i>
              <!--文本-->
              <span>{{item.authName}}</span>
            </template>
            <!--二级菜单-->
<!--:index="转跳路径" 当开启了路由模式之后,点击会转跳到路由指定的路径-->
            <el-menu-item :index="'/'+subItem.path" v-for="subItem in item.children" :key="subItem.id" @click="saveNavState('/'+subItem.path)">
              <template slot="title">
                <!--图标-->
                <i class="el-icon-menu"></i>
                <!--文本-->
                <span>{{subItem.authName}}</span>
              </template>
            </el-menu-item>
          </el-submenu>
          <el-menu-item index="2">
          </el-menu-item>
        </el-menu>
      </el-aside>
<!--右侧内容主体-->
      <el-main><!--在main主体区放一个路由占位符-->
      <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
    // 左侧菜单数据
      menulist: [],
      iconList: {
        125: 'el-icon-user-solid',
        103: 'el-icon-s-operation',
        101: 'el-icon-s-goods',
        102: 'el-icon-s-order',
        145: 'el-icon-s-data'
      },
      // 是否折叠
      isCollapse: false,
      // 被激活的连接地址
      activePath: ''
    }
  },
  name: 'Home.vue',
  // 请求初始化数据 methods方法
  methods: {
    logout () {
      // 点击删除token
      window.sessionStorage.clear()
      // 删除token后强制转跳到登录界面(login)
      this.$router.push('/login')
    },
    // 获取所有菜单
    async getMenuList () {
      const { data: res } = await this.$http.get('menus')
      if (res.meta.status !== 200) return this.$massage.error(res.meta.msg)
      // 把数据赋值给menulist数组中
      this.menulist = res.data
      console.log(res)
    },
    // 点击按钮切换折叠与展开
    toggleCollapse () {
      // 点击toggleCollapse函数 把我们设定的数值false重置， true = ！ false
      this.isCollapse = !this.isCollapse
    },
    // 保存连接的激活状态
    saveNavState (activePath) {
    // 点击后传过来的数值保存到sessionStorage中
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    }
  },
  // 调用渲染好的数据，created创建 mounted加载 updated更新 destroyed销毁
  created () {
    this.getMenuList()
    this.activePath = window.sessionStorage.getItem('activePath')
  }
}
</script>
