<template>
<div>
<!--面包屑导航区-->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    <el-breadcrumb-item>用户列表</el-breadcrumb-item>
  </el-breadcrumb>
  <!--卡片组件区-->
  <el-card>
<!--搜索与添加区域-->
    <el-row :gutter="20">
      <el-col span="7">
        <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @change="getUserList">
        <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
      </el-input>
      </el-col>
      <el-col span="4">
        <el-button type="primary" @click="addDialogVisible = true">添加用户</el-button>
      </el-col>
    </el-row>
<!-- 用户列表区-->
<!--data指定数据来源-->
    <el-table :data="userlist" stripe border>
<!--prop指定数据来源-->
<!--type="index"索引列-->
      <el-table-column type="index"></el-table-column>
      <el-table-column label="姓名" prop="username"></el-table-column>
      <el-table-column label="邮箱" prop="email"></el-table-column>
      <el-table-column label="电话" prop="mobile"></el-table-column>
      <el-table-column label="角色" prop="role_name"></el-table-column>
      <el-table-column label="状态" prop="mg_state">
<!--scopr 获取表单信息-->
        <template slot-scope="scope">
<!--{{scope.row}}-->
<!--{{scope.row.mg_state}}-->
          <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="180px">
        <template slot-scope="scope">
          <!--修改按钮-->
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
          <!--删除按钮-->
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
          <!--分配角色按钮-->
          <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="selectrole(scope.row)"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
<!--分页区域-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
<!--添加用户对话框-->
<!--当el-dialog关闭对话框 则触发@close事件,弹窗-->
  <el-dialog
    title="添加用户"
    :visible.sync="addDialogVisible"
    width="50%" @close="addDialogClosed">
<!--内容主体区-->
<!--prop 指定校验规则-->
    <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
      <el-form-item label="用户名" prop="username">
        <el-input v-model="addForm.username"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="addForm.password"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="addForm.email"></el-input>
      </el-form-item>
      <el-form-item label="手机" prop="mobile">
        <el-input v-model="addForm.mobile"></el-input>
      </el-form-item>
    </el-form>
      <!--底部区-->
    <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false">取 消</el-button>
<!--addDialogVisible = false-->
    <el-button type="primary" @click="addUser">确 定</el-button>
  </span>
  </el-dialog>
<!--修改用户信息-->
  <el-dialog
    title="修改用户信息"
    :visible.sync="editDialogVisible"
    width="50%"
    @close="editDialogClose">
    <!--:model="editForm" 绑定的数据应该是data()中的editForm-->
    <!--rules是验证规则-->
    <!--ref 是当前表单的引用对象-->
    <!--disabled禁用不可修改-->
    <!--prop 既是数据来源，也是校验规则-->
    <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
      <el-form-item label="用户名">
        <el-input v-model="editForm.username" disabled></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="editForm.email"></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="mobile">
        <el-input v-model="editForm.mobile"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
<!--editDialogVisible = false-->
    <el-button type="primary" @click="editUserInfo">确 定</el-button>
    </span>
  </el-dialog>
<!--分配角色-->
  <el-dialog
    title="提示"
    :visible.sync="selectroledialogVisible"
    width="50%"
  @close="setRoleDialogCloseed">
    <div>
      <p>当前用户：{{userInfo.username}}</p>
      <p>当前角色：{{userInfo.role_name}}</p>
      <p>分配新角色：
<!--v-model 双向绑定-->
<!--用户选择了数值之后会保存到selectedRoleId-->
        <el-select v-model="selectedRoleId" placeholder="请选择">
          <el-option
            v-for="item in roleList"
            :key="item.id"
            :label="item.roleName"
            :value="item.id">
          </el-option>
        </el-select></p>
    </div>
    <span slot="footer" class="dialog-footer">
    <el-button @click="selectroledialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="saveRoleInfo">确 定</el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: 'users',
  // 存放私有数据
  data () {
    // 邮箱验证规则
    var checkEmail = (rule, value, cb) => {
      // 验证邮箱的正则表达式
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      // 如果返回true,则说明符合正则表达式 ，则通过
      if (regEmail.test(value)) {
        // 合法邮箱
        return cb()
      }
      // 如果不通返回false 则不是合法邮箱。 抛出错误
      cb(new Error('请输入合法的邮箱'))
    }
    // 手机号码验证规则
    var checkMobile = (rule, value, cb) => {
      // value 数值，cb 回调函数
      // 验证手机号的正则表达式
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      // 如果比对返回true 则说明符合正则表达式验证.则通过
      if (regMobile.test(value)) {
        return cb()
      }
      // 如果不符合正则表达式,则不通过，不通过则需要抛出错误，并提示
      cb(new Error('请输入合法的手机号码'))
    }
    return {
      // 获取用户列表的参数对象
      queryInfo: {
        query: '',
        // 当前页码数值
        pagenum: 1,
        // 每页显示几条数据
        pagesize: 10
      },
      // 所有用户数据
      userlist: [],
      total: 0,
      // 控制添加用对话框户的显示与隐藏
      addDialogVisible: false,
      // 控制修改用户对话框的显示与隐藏
      editDialogVisible: false,
      // 角色分配对话框的显示与隐藏
      selectroledialogVisible: false,
      // 添加用户的表单数据
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 根据ID查询到的信息对象 只有一组数据
      editForm: {},
      // 添加表单的验证规则
      addFormRules: {
        username: [
          // trigger当失去焦点的时候 抛出异常 required必填字段
          { required: true, message: '请输入用户邮箱', trigger: 'blur' },
          { min: 3, max: 10, message: '用户名的长度在3~4个字符之间', trigger: 'blur' }
        ],
        password: [
          // trigger当失去焦点的时候 抛出异常 required必填字段
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 3, max: 10, message: '密码的长度在3~4个字符之间', trigger: 'blur' }
        ],
        email: [
          // trigger当失去焦点的时候 抛出异常 required必填字段
          { required: true, message: '请输入邮箱', trigger: 'blur' }, { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号码', trigger: 'blur' }, { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 修改表单验证规则对象
      editFormRules: {
        email: [
          // trigger 失去焦点触发 message
          { required: true, message: '请输入合法的邮箱', trigger: 'blur' },
          // 书写email校验正则表达式
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          // trigger 失去焦点触发 message
          {
            required: true, message: '请输入合法的手机号码', trigger: 'blur'
          },
          // 书写mobile正则表达式
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 需要被分配角色的用户信息
      userInfo: {},
      // 所有角色的数据列表
      roleList: [],
      // 已选中的角色ID数值
      selectedRoleId: ''
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    async getUserList () {
      const { data: res } = await this.$http.get('users', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('获取用户列表失败')
      }
      // 把数据赋值给userlist 方便解析
      this.userlist = res.data.users
      console.log(this.userlist)
      // 把数据赋值给total 方便解析
      this.total = res.data.total
      console.log(res)
    },
    // 监听pageSize 改变事件
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // 监听 页码值 改变事件
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      // 刷新商品列表
      this.getUserList()
    },
    // 监听开关的状态
    async userStateChanged (userinfo) {
      // 请求返回整个data数据,并且重命名为res
      const { data: res } = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
      // 打印res
      // console.log(res)
      // console.log(userinfo.mg_state)
      // 判断状态码是否为200,不为200说明更新失败,则需抛出错误
      if (res.meta.status !== 200) {
        // 取反
        userinfo.mg_state = !userinfo.mg_state
        console.log(userinfo.mg_state)
        return this.$message.error('更新失败')
      }
      // 添加用户对话框的关闭事件
      this.$message.success('更新成功')
      console.log(res.meta.msg)
    },
    addDialogClosed () {
      // 拿到表单的引用$refs.addFormRef 调用resetFields 完成关闭重置表单
      this.$refs.addFormRef.resetFields()
    },
    // 点击按钮添加新用户
    addUser: function () {
      this.$refs.addFormRef.validate(async valid => {
        // 首先要对表单进行预处理，通过了才允许发起请求
        if (!valid) return
        // 发起post请求 并且返回数据data. 并且重命名为 res
        const { data: res } = await this.$http.post('users', this.addForm)
        // 判断添加数据时返回的是否为201 , 如果为201 则添加成功 ,否则添加失败
        if (res.meta.status !== 201) {
          return this.$message.error('添加失败')
        }
        this.$message.success('添加成功')
        this.addDialogVisible = false
        // 隐藏添加用户的对话框
        this.addDialogClosed()
      })
    },
    // 点击展示编辑用户的对话框
    async showEditDialog (id) {
      // 点击展开用户修改对话框
      this.editDialogVisible = true
      // 打印按钮传过来的ID是多少
      console.log(id)
      // 根据ID进行get请求，返回数据
      const { data: res } = await this.$http.get('users/' + id)
      // 判断返回的状态码是否为 200
      if (res.meta.status !== 200) { return this.$message.error('查询用户信息失败') }
      // 如果是返回的状态码是200 则在私有数据对象中创建私有数据对象保存返回的数据
      this.editForm = res.data
      console.log(this.editForm)
    },
    // 修改用户信息并提交
    editUserInfo: function () {
      this.$refs.editFormRef.validate(async valid => {
        // 对表单进行预处理
        // console.log(valid)
        if (!valid) return
        // 发起修改用户信息的请求 put
        const { data: res } = await this.$http.put('users/' + this.editForm.id, {
          email: this.editForm.email, mobile: this.editForm.mobile
        })
        console.log(this.editForm.id)
        if (res.meta.status !== 200) { return this.$message.error('修改失败') }
        this.$message.success('更新成功')
        // 隐藏对话框
        this.editDialogVisible = false
        this.editDialogClose()
        // 刷新用户列表
        this.getUserList()
      })
    },
    // 监听修改对话框的关闭事件
    editDialogClose () {
      // 重置操作
      this.$refs.editFormRef.resetFields()
    },
    // 分配角色
    async selectrole (userInfo) {
      // 点击弹窗
      this.selectroledialogVisible = true
      this.userInfo = userInfo
      // 在展示对话框之前，获取所有的角色列表
      const { data: res } = await this.$http.get('roles')
      console.log(userInfo)
      console.log('以上是selectorle获取到的用户信息')
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      this.roleList = res.data
    },
    // 根据ID删除对应的用户信息
    async removeUserById (id) {
      // 弹框提示是否删除
      const res = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
        // 如果不用catch捕获错误消息,则会报错
      }).catch(err => err)
      // 如果用户确认删除则返回字符串 confirm
      // 如果用户取消删除则返回字符串 cancel
      console.log(res)
      // 对返回的字符串进行判断
      if (res !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: result } = await this.$http.delete('users/' + id)
      if (result.meta.status !== 200) { return this.$message.error('删除失败') }
      // 抛出删除成功的提示
      this.$message.success('删除成功')
      // 刷新列表
      this.getUserList()
    },
    // 点击按钮分配角色
    async saveRoleInfo () {
      // 判断用户是否有选择
      if (!this.selectedRoleId) {
        return this.$message.error('用户未选择')
      }
      // 发起请求
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.selectedRoleId
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('分配角色失败')
      }
      this.$message.success('分配角色成功')
      //  刷新用户列表
      this.getUserList()
      // 隐藏对话框
      this.selectroledialogVisible = false
    },
    // 监听用户选择框
    setRoleDialogCloseed () {
      // 把选项ID置空 ， 当第二次点击的时候就不会显示上一次的修改选项
      this.selectedRoleId = ''
      this.userInfo = {}
    }
  }
}
</script>
<style lang="less" scoped>
.text {
  font-size: 14px;
}
.item {
  padding: 18px 0;
}
.box-card {
  width: 480px;
}
.el-pagination{
  margin-top: 15px;
}
</style>
