<script>
export default {
  name: 'params',
  data () {
    return {
      // 分类参数数据
      cateList: [],
      // 级联选择器配置对象
      cateProps: {
        // 指定选中的值
        value: 'cat_id',
        // 所看到的值
        label: 'cat_name',
        // 父子节点的嵌套属性
        children: 'children'
      },
      // 选中父级分类的ID数组
      selectKeys: [],
      // 被激活的标签页的名称
      // 默认激活动态参数面板
      activeName: 'many',
      // 动态参数数据
      manyTableData: [],
      // 静态参数数据
      onlyTableData: [],
      // 控制修改对话框的展开和关闭
      editdialogVisible: false,
      // 控制添加对话框的展开和关闭
      addDialogVisible: false,
      // 添加参数的表单数据对象
      addform: {
        attr_name: ''
      },
      // 添加表单的验证规则对象
      addformrules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      },
      //  修改表单
      editForm: {
        attr_name: ''
      },
      // 修改对话框的验证规则
      editFormRules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      }
      // // 控制添加动态参数的对话框
      // addynamicvisible: false,
      // // 控制添加静态参数的对话框
      // addstaticdialogVisible: false,
      // // 添加动态属性的表单
      // addynamicform: [],
      // // 添加静态属性的表单
      // addstaticForm: []

    }
  },
  created () {
    // 刷新分类列表
    this.getCateList()
  },
  methods: {
    // 获取所有分类参数
    async getCateList () {
      // 发起请求
      const { data: res } = await this.$http.get('categories')
      // 判断返回的状态码
      if (res.meta.status !== 200) {
        this.$message.error('获取分类参数失败')
      }
      this.cateList = res.data
      console.log(this.cateList)
    },
    // 级联选择框 选择项发生变化触发这个函数
    // 分类数据处理
    parentCateChanged () {
      this.getParamsData()
    },
    // tab 标签页点击事件的处理函数
    handleTabClick () {
      this.getParamsData()
      // console.log(this.activeName)
    },
    // 获取参数的列表数据
    async getParamsData () {
      // 输出用户所选的选项ID
      // console.log(this.manyTableData)
      // 如果不等于三，则用户选择的不是三级分类
      if (this.selectKeys.length !== 3) {
        this.$message.error('选择的不是三级分类')
        // 清空重置
        this.selectKeys = []
        this.manyTableData = []
        this.onlyTableData = []
      }
      // 证明选中的是三级分类
      // 根据所选分类的ID 和当前的所处的面板获取对应的参数
      // 应为传进去的是两个参数,一个是many 一个是only 所以返回的数据是不一样的
      const { data: res } = await this.$http.get(`categories/${this.cateID}/attributes`, { params: { sel: this.activeName } })
      if (res.meta.status !== 200) {
        this.$message.error('获取参数列表失败')
      }
      // 将res.data中的每一项都做一个循环
      // 每循环一次都会拿到一个item对象
      // res.data 就是item
      res.data.forEach(item => {
        // 每拿到一个item对象,就用split做分割
        // ? 问号指的是,是否为空,否则就返回一个空白数组
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 循环前是一个字符串,循环后是一个数组
        // 控制文本框的显示与隐藏 默认为隐藏
        // 每进行一次就获得一个单独的inputVisible和inputValue
        item.inputVisible = false
        // 文本框中输入的值
        item.inputValue = ''
      })
      console.log(res.data)
      // 对数据进行判断，判断是动态还是静态
      if (this.activeName === 'many') {
        // 进行赋值
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 将旧数据放到文本框中
    async editPar (id) {
      // 点击展开弹窗
      this.editdialogVisible = true
      // 发起请求
      // this.cateID（分类ID） id（参数ID）
      const { data: res } = await this.$http.get(`categories/${this.cateID}/attributes/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('获取参数信息失败')
      }
      console.log(res.data)
      // 获取成功，将数值赋值给双向绑定的v-model
      this.editForm = res.data
    },
    // 编辑提交参数
    async submitPar () {
      this.$refs.editFormRef.validate(async valid => {
        // 判断是否为空
        if (!valid) return
        // 分类cat_id 和 参数ID 以及参数名称均来自于点击编辑时传进去的参数，保存到editForm中进行调用
        const { data: res } = await this.$http.put(`categories/${this.editForm.cat_id}/attributes/${this.editForm.attr_id}`, { attr_sel: this.activeName, attr_name: this.editForm.attr_name })
        console.log(res)
        if (res.meta.status !== 200) {
          return this.$message.error('更新失败')
        }
        this.$message.success('更新成功')
        // 更新分类参数列表
        this.getParamsData()
        // 完成修改后，关闭窗口
        this.editdialogVisible = false
      })
    },
    // 根据ID删除分类
    async deletePar (id) {
      console.log(id.attr_id)
      // 弹窗点击是否删除
      const res = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
        // 如果不用catch捕获错误消息,则会报错
      }).catch(err => err)
      // 如果用户确认删除则返回字符串 confirm
      // 如果用户取消删除则返回字符串 cancel
      console.log(res)
      if (res !== 'confirm') {
        this.$message.info('已经取消删除')
      }
      // id.cat_id 分类ID
      // id.attr_id 参数ID
      const { data: result } = await this.$http.delete(`categories/${id.cat_id}/attributes/${id.attr_id}`, { params: { attr_sel: this.activeName } })
      if (result.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
      // 刷新属性列表
      this.getParamsData()
    },
    // 监听对话框的关闭事件
    adddialogClose () {
      this.$refs.addformRef.resetFields()
    },
    // 添加商品分类
    addParams () {
      // 表单预验证
      this.$refs.addformRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(`categories/${this.cateID}/attributes`, {
          attr_name: this.addform.attr_name,
          attr_sel: this.activeName
        })
        console.log(res.data)
        if (res.meta.status !== 201) {
          return this.$message.error('添加失败')
        }
        this.$message.success('添加成功')
        // 刷新列表
        this.getParamsData()
        // 添加完成之后关闭对话框
        this.addDialogVisible = false
      })
    },
    showInput (row) {
      row.inputVisible = true
      // 让文本框自动获得焦点
      // $nextTick 方法的作用,就是当页面上元素被重新渲染之后,才会执行回调函数中的代码
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 文本框失去焦点,或者按下了回车键触发的函数
    async handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        // 输入的字符不合法,应该重置为空
        row.inputValue = ''
        // 并且隐藏文本框
        row.inputVisible = false
        return
      }
      // 如果没有return ,则证明输入的内容,需要做后续处理
      row.attr_vals.push(row.inputValue.trim())
      // 将文本框的字符重置为空
      row.inputValue = ''
      // 关闭文本框
      row.inputVisible = false
      // 需要发起请求保存这次操作
      const { data: res } = await this.$http.put(`categories/${this.cateID}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        // 每一项有空格拼接
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('更新失败')
      }
      this.$message.success('更新成功')
    },
    // 将对attr_vals的操作保存到数据库
    async saveAtterVals (row) {
      // 需要发起请求保存这次操作
      const { data: res } = await this.$http.put(`categories/${this.cateID}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        // 每一项有空格拼接
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
    },
    // 删除对应的参数可选项
    handleClose (i, row) {
      // 删除操作
      row.attr_vals.splice(i, 1)
      this.saveAtterVals(row)
    }
  },
  // 计算器
  computed: {
    // 如果按钮需要被禁用，则返回true 否则返回false
    isBtnDisabled () {
      if (this.selectKeys.length !== 3) {
        return true
      }
      return false
    },
    // 当前选中的三级分类的ID
    cateID () {
      if (this.selectKeys.length === 3) {
        // selectKeys 三级分类也就是最后一个的索引为2
        return this.selectKeys[2]
      }
      return null
    },
    // 动态计算标题文本
    titleText () {
      if (this.activeName === 'many') {
        return '动态参数'
      }
      return '静态参数'
    },
    // 动态计算文本
    label () {
      if (this.activeName === 'many') {
        return '动态'
      }
      return '静态'
    }
  }
}

</script>

