<template>
<div>
<!--面包屑导航-->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>商品管理</el-breadcrumb-item>
    <el-breadcrumb-item>商品分类</el-breadcrumb-item>
  </el-breadcrumb>
<!--卡片组件区-->
  <el-card>
    <el-row>
      <el-col>
<!--添加分类按钮-->
        <el-button type="primary" @click="addCategory()">添加分类</el-button>
      </el-col>
    </el-row>
<!--表格区域-->
    <tree-table style="margin-top: 15px"
      class="treeTable"
      :data="cateList"
      :columns="columns"
      :selection-type="false"
      :expand-type="false"
      show-index
      index-text="#"
      border
      :show-row-hover="false">
<!--是否有效-->
      <template slot="isok" slot-scope="scope">
      <i class="el-icon-check" v-if="scope.row.cat_deleted === false" style="color:springgreen"></i>
      <i class="el-icon-close" v-if="scope.row.cat_deleted === true" style="color: red"></i>
      </template>
<!-- 排序-->
      <template slot="order" slot-scope="scope">
        <el-tag v-if="scope.row.cat_level === 0" size="mini">一级</el-tag>
        <el-tag type="success" v-if="scope.row.cat_level === 1" size="mini">二级</el-tag>
        <el-tag type="warning" v-if="scope.row.cat_level === 2" size="mini">三级</el-tag>
      </template>
<!--操作-->
      <template slot="opt" slot-scope="scope">
        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.cat_id)">编辑</el-button>
        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removecateById(scope.row.cat_id)" >删除</el-button>
      </template>
    </tree-table>
    <!--分页区域-->
    <!--size-change监听当前条目数-->
    <!--current-change监听当前页码数-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="querInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="querInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
<!--修改对话框-->
  <el-dialog
    title="修改分类名称"
    :visible.sync="editDialogVisible"
    width="50%">
    <el-form :model="editForm" ref="editFormRef" label-width="100px">
      <el-form-item label="分类名称">
        <el-input v-model="editForm.cat_name"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editCate">确 定</el-button>
  </span>
  </el-dialog>
<!--添加分类对话框-->
  <el-dialog
    title="添加分类"
    :visible.sync="addCategorydialogVisible"
    width="50%"
    @close="closeCateaddForm">
<!--添加分类表单-->
<!--:model v-model 数据来源和去向-->
<!--rules 校验规则-->
<!--ref 引用对象-->
<!--v-model 双向绑定-->
    <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
      <el-form-item label="分类名称" prop="cat_name">
        <el-input v-model="addCateForm.cat_name"></el-input>
      </el-form-item>
      <el-form-item label="父级分类">
<!--options 用于指定数据源-->
<!--Pros 用来指定配置对象-->
<!--v-model 双向绑定 获取用户选择的选项的ID值-->
<!--props指定级联选择器的配置对象 每个对象对应的数据-->
<!--clearable 允许删除-->
<!--change-on-select 允许选中任意一项 默认值false-->
        <el-cascader
          v-model="selecteKeys"
          :options="feater"
          :props="casccaderPros"
          clearable
          @change="parentCateChanged"
        change-on-select>
        </el-cascader>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="addCategorydialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addCate">确 定</el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: 'categories',
  // 私有变量
  data () {
    return {
      // 查询条件
      querInfo: {
        type: 1,
        // 默认获取第一页
        pagenum: 1,
        // 每页显示多少条数据
        pagesize: 5
      },
      // 空数组 商品分类的数据列表，默认为空
      cateList: [],
      // 总数据条数
      total: 0,
      // 为table指定列的定义
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name'
        },
        {
          label: '是否有效',
          // 将当前列定义为模板列
          type: 'template',
          // 表示当前这一列模板的使用名称
          template: 'isok'
        },
        {
          label: '排序',
          // 将当且定义为模板列
          type: 'template',
          // 表示当前这一列模板的使用名称
          template: 'order'
        },
        {
          label: '操作',
          // 将当且定义为模板列
          type: 'template',
          // 表示当前这一列模板的使用名称
          template: 'opt'
        }
      ],
      // 控制修改对话框的展开和隐藏
      editDialogVisible: false,
      // 控制添加分类对话框的展开和隐藏
      addCategorydialogVisible: false,
      // 添加分类表单的数据对象
      addCateForm: {
        // 将要添加分类的名字
        cat_name: '',
        // 父级分类ID
        cat_pid: 0,
        // 默认添加一级分类
        cat_level: 0
      },
      // 添加分类表单的验证规则对象
      addCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      // 空数组 父级分类数据列表
      feater: [],
      // 指定级联选择器的配置对象
      casccaderPros: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 选中的父级分类ID数组
      selecteKeys: [],
      // 根据I查询到的信息对象 只有一组
      editForm: {}
    }
  },
  created () {
    this.getcateList()
    // 获取父级的数据列表
    this.getParentList()
  },
  methods: {
    // 获取商品分类数据
    async getcateList () {
      const { data: res } = await this.$http.get('categories', { params: this.querInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类失败')
      }
      // 如果请求成功，则把商品分类存储在cateList中
      // this.$message.success('成功获取商品分类')
      // res.data 是一个对象，对象的result中存放着商品数据列表 所以要.result
      this.cateList = res.data.result
      console.log(this.cateList)
      // 把总的数据条目数赋值给私有变量total
      this.total = res.data.total
    },
    // 监听每页显示多少条数据 改变事件
    handleSizeChange (newSize) {
      this.querInfo.pagesize = newSize
      //  刷新商品列表
      this.getcateList()
    },
    // 监听页码值 改变事件
    handleCurrentChange (newPage) {
      this.querInfo.pagenum = newPage
      // 刷新商品列表
      this.getcateList()
    },
    // 点击展示编辑对话框
    async showEditDialog (id) {
      // 打开对话框
      this.editDialogVisible = true
      const { data: res } = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('查询失败')
      }
      console.log(res.data)
      this.editForm = res.data
      console.log(this.editForm.cat_name)
    },
    // 修改商品分类信息并且提交
    async editCate () {
      // 表单预验证
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 发起修改用户信息的请求 put
        const { data: res } = await this.$http.put('categories/' + this.editForm.cat_id, {
          cat_name: this.editForm.cat_name
        })
        if (res.meta.status !== 200) {
          return this.$message.error('修改分类失败')
        }
        this.$message.success('修改分类成功')
        // 刷新商品分类列表
        this.getcateList()
        // 关闭对话框
        this.editDialogVisible = false
      })
    },
    // 根据ID删除对应的商品
    async removecateById (id) {
      console.log(id)
      // 弹窗提示是否删除
      const res = await this.$confirm('此操作将永久删除该分类,是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      // 进行判断,如果点击确定则发送请求,如果点击取消,则弹窗提示 info('已取消删除')
      console.log(res)
      // 取消返回的是cancel
      // 确定返回的是confirm
      if (res !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: result } = await this.$http.delete('categories/' + id)
      if (result.meta.status !== 200) {
        this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
      // 刷新商品列表
      this.getcateList()
    },
    // 点击展示对话框
    async addCategory () {
      // 获取父级分类的数据列表
      this.getParentList()
      this.addCategorydialogVisible = true
    },
    // 获取父级的数据列表
    async getParentList () {
      const { data: res } = await this.$http.get('categories', {
        params: {
          type: 2
        }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取父级数据列表失败')
      }
      this.$message.success('成功获取父级数据列表')
      // 如果请求状态码为200 则把返回的数据赋值给私有变量中的feater
      this.feater = res.data
      console.log(this.feater)
    },
    // 选择项发生变化触发这个函数
    // 处理添加分类的数据处理
    parentCateChanged () {
      console.log(this.selecteKeys)
      // 如果selectedKeys 数组中的 长度(lenth)大于 0 , 证明选中了父级分类
      // 反之没有选中任何父级分类
      if (this.selecteKeys.length > 0) {
        // 取得父级分类ID 赋值给addCateForm双向绑定的表单里边的cat_pid 键
        this.addCateForm.cat_pid = this.selecteKeys[this.selecteKeys.length - 1]
        // 为当前分类的等级赋值1
        this.addCateForm.cat_level = this.selecteKeys.length
      } else {
        // 没有选中选项
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 点击按钮，添加新的分类
    async addCate () {
      // 对表单进行预验证
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) {
          return this.$message.error('添加分类失败')
        }
        this.$message.success('添加分类成功')
        // 刷新商品分类列表
        this.getcateList()
        // 关闭对话框
        this.addCategorydialogVisible = false
      })
      console.log(this.addCateForm)
    },
    // 点击关闭对话框，重置表单数据
    closeCateaddForm () {
      // 重置addCateFormRef 表单数据
      this.$refs.addCateFormRef.resetFields()
      // 将select中选中的ID清空
      this.selecteKeys = []
      // 将addCateForm表单中的cat_level 和 cat_pid 中的数值清空定义为0
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    }
  }
}
</script>
<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
