<template>
<div>
<!--面包屑导航 -->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>商品管理</el-breadcrumb-item>
    <el-breadcrumb-item>商品列表</el-breadcrumb-item>
  </el-breadcrumb>
<!--卡片组件区-->
  <el-card>
    <el-row :gutter="20">
<!--搜索文本输入框-->
      <el-col :span="8">
<!--当这一行发生改变时, 触发@change事件，重新调用getgoodsList 获取所有商品数据-->
<!--v-model双向绑定数据给input-->
        <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @change="getgoodsList">
<!--slot="append 搜索按钮"-->
          <el-button slot="append" icon="el-icon-search" @click="getgoodsList"></el-button>
        </el-input>
      </el-col>
<!--添加按钮-->
      <el-col :span="4">
<!-- 因为添加无需传入参数，所以不用创建独立的函数来控制他的展开和隐藏-->
        <el-button type="primary" @click="goAddpage">添加商品</el-button>
      </el-col>
    </el-row>
<!--商品数据列表区-->
<!--:data 数据来源-->
    <el-table
      :data="goodsList"
      style="width: 100%" border stripe>
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="goods_name" label="商品名称"></el-table-column>
      <el-table-column prop="goods_price" label="商品价格(元)" width="95px"></el-table-column>
      <el-table-column prop="goods_number" label="商品数量" width="70px"></el-table-column>
      <el-table-column prop="add_time" label="创建时间" width="140px">
        <template slot-scope="scope">
          {{scope.row.add_time | dataFormat}}
        </template>
      </el-table-column>
      <el-table-column label="操作" width="130px">
<!--作用域插槽-->
        <template slot-scope="scope">
<!--修改按钮-->
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="editGoods(scope.row.goods_id)"></el-button>
<!--删除按钮-->
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteGoods(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
<!--分页功能-->
<!--当size 显示条目数发生了变化之后，就调用 handleSizeChange 函数,此函数能拿到最新的 条目数-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
<!--添加商品-->
  <el-dialog
    title="提示"
    :visible.sync="adddGoodsialogVisible"
    width="50%">
    <span>这是一段信息</span>
    <span slot="footer" class="dialog-footer">
    <el-button @click="adddGoodsialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="adddGoodsialogVisible = false">确 定</el-button>
  </span>
  </el-dialog>
<!--修改商品信息-->
  <el-dialog
    title="提示"
    :visible.sync="editGoodsdialogVisible"
    width="50%">
    <span>这是一段信息</span>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editGoodsdialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editGoodsdialogVisible = false">确 定</el-button>
  </span>
  </el-dialog>

</div>
</template>

<script>
export default {
  name: 'goods',
  data () {
    return {
      // 获取商品列表的参数对象
      queryInfo: {
        // 商品名称
        query: '',
        // 当前页码数
        pagenum: 1,
        // 当前显示条目数
        pagesize: 10
      },
      // 所有商品列表数据
      goodsList: [],
      // 总数据条目数
      total: 0,
      // 控制添加商品对话框的展开和隐藏
      adddGoodsialogVisible: false,
      // 控制修改商品信息对话框的展开和隐藏
      editGoodsdialogVisible: false
    }
  },
  created () {
    this.getgoodsList()
  },
  methods: {
    // 请求获取所有商品列表
    async getgoodsList () {
      const { data: res } = await this.$http.get('goods', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败')
      }
      this.$message.success('获取商品列表成功')
      //  将获取到的商品列表数据赋值给goodsList
      this.goodsList = res.data.goods
      console.log(res.data)
      // 把数据赋值给total ,用于分页条目数
      this.total = res.data.total
    },
    // 当条目数发生了改变 触发了这个函数之后就会拿到最新的 条目数，把条目数赋值给return中的query中的pagesize
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getgoodsList()
    },
    // 当页码数发生了改变 触发了这个函数之后就能拿到最新的 页码数 把页码数赋值给return中的query中的pagenum
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getgoodsList()
    },
    // 根据ID删除商品
    async deleteGoods (row) {
      const result = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      // 判断result 返回回来的字符串
      // 选择确定 返回的字符串是confirm
      // 选择取消 返回的字符串是cancel
      if (result !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      // 发起删除请求
      const { data: res } = await this.$http.delete('goods/' + row.goods_id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除商品失败')
      }
      this.$message.success('删除商品成功')
      // 刷新列表
      this.getgoodsList()
    },
    // 点击展开修改对话框，并且把当行的数据填在input上
    async editGoods (id) {
      // 点击展开修改商品对话框
      this.editGoodsdialogVisible = true
      // 发起请求根据ID查询商品信息
      const { data: res } = await this.$http.get('goods/' + id)
      console.log(res.data)
    },
    // 商品添加页面
    goAddpage () {
      // push强制转跳到商品添加页面 goods/add
      this.$router.push('/goods/Add')
    },
    search (qwe) {
      console.log(qwe)
    }
  }
}
</script>

<style lang="less" scoped>

</style>
